.main-header {
  background-image: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  position: sticky;
  min-height: 120px;
  top: 0;
  z-index: 10;

  @media (max-width: $tablet-width) {
    position: relative;
  }

  @media (max-width: $mobile-width) {
    min-height: 70px;
  }
}
