.accordion {
  margin: 0 auto 50px auto;
}
.accordion .accordion {
  width: 90%;
  margin: 0 auto;
}
.accordion__element:last-child {
  margin-bottom: 0;
}
.accordion__element {
  margin-bottom: 34px;
  padding: 10px;
}
.accordion__element.is-active > .accordion__button {
  opacity: 0.5;
}
.accordion__button {
  width: 100%;
  padding: 10px;
  text-transform: uppercase;
  cursor: pointer;
}
.accordion__wrapper {
  padding: 10px;
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=element].is-active > [data-accordion=button]::before {
  transform: rotate(-180deg);
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-bold.woff2") format("woff2"), url("../fonts/roboto-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  /* line-height: 24px; */
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

p {
  margin: 0;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 110px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(3, 16, 27, 0.7);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
  font-family: "PT Mono", "Times New Roman", serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.modal__content {
  position: relative;
  width: 500px;
  padding: 36px 40px 44px;
  background-color: #00b3ff;
  border-radius: 3px;
  transform: translateX(-8px);
  margin: auto 0;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__content h2 {
  margin: 0;
  margin-bottom: -15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 43px;
}
.modal__content p {
  margin: 0;
  margin-bottom: 20px;
}
.modal__content form {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}

.modal__close-btn {
  position: absolute;
  top: 17px;
  right: 17px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  border-radius: 3px;
}
.modal__close-btn::after, .modal__close-btn::before {
  content: "";
  position: absolute;
  top: 47%;
  left: 14%;
  display: block;
  width: 28px;
  height: 1px;
  background-color: #ffffff;
  transform: rotate(45deg);
}
.modal__close-btn::before {
  transform: rotate(-45deg);
}
.modal__close-btn:hover {
  background-color: rgba(255, 135, 0, 0.5);
  opacity: 0.8;
}
.modal__close-btn:focus-visible {
  outline: none;
  background-color: rgba(255, 135, 0, 0.5);
  opacity: 0.8;
}
.modal__close-btn:active {
  opacity: 1;
}

.modal__input label.is-invalid span {
  color: #ff0000;
}
.modal__input input:not([type=checkbox]),
.modal__input textarea {
  width: 100%;
  background-color: rgba(4, 24, 39, 0.15);
  border-radius: 3px;
  border: none;
}
.modal__input input:not([type=checkbox])::placeholder,
.modal__input textarea::placeholder {
  color: #ffffff;
}
.modal__input input:not([type=checkbox]):hover, .modal__input input:not([type=checkbox]):focus,
.modal__input textarea:hover,
.modal__input textarea:focus {
  outline: none;
  box-shadow: 0 0 8px #ff8700;
}
.modal__input input:not([type=checkbox]):active, .modal__input input:not([type=checkbox]):focus-within,
.modal__input textarea:active,
.modal__input textarea:focus-within {
  color: #ffffff;
}
.modal__input input:not([type=checkbox]):disabled,
.modal__input textarea:disabled {
  background-color: #c4c4c4;
}
.modal__input input:not([type=checkbox]) {
  padding: 15px;
}
.modal__input textarea {
  min-height: 155px;
  padding: 25px 15px;
}
.modal__input--checkbox {
  padding-left: 10px;
  order: 5;
}
.modal__input--checkbox label {
  display: flex;
  align-items: center;
}
.modal__input--checkbox input {
  appearance: none;
  position: relative;
  display: block;
  width: 15px;
  height: 15px;
  margin-right: 10px;
  border: 1px solid #ffffff;
  border-radius: 2px;
}
.modal__input--checkbox input:checked::after {
  content: "";
  position: absolute;
  left: 27%;
  top: 13%;
  width: 5px;
  height: 7px;
  border: solid #ffffff;
  border-width: 0 1px 1px 0;
  transform: rotate(45deg);
}
.modal__input--checkbox input:hover, .modal__input--checkbox input:focus {
  outline: none;
  border-color: rgba(255, 135, 0, 0.5);
  box-shadow: 1px 1px 1px #ff8700;
}
.modal__input--checkbox input:disabled {
  border: none;
  background-color: #c4c4c4;
}
.modal__input--checkbox span {
  margin: 0;
  font-size: 14px;
  line-height: 15px;
}

.modal__submit-button {
  user-select: none;
  width: 100%;
  display: inline-block;
  border: none;
  cursor: pointer;
  padding: 13px 60px;
  border-radius: 3px;
  color: #ffffff;
  background-color: #041427;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
.modal__submit-button:hover {
  box-shadow: 0 0 8px #ff8700;
}
.modal__submit-button:focus-visible {
  outline: none;
  box-shadow: 0 0 8px #ff8700;
}
.modal__submit-button:active {
  opacity: 0.7;
  background-color: #ff8700;
  box-shadow: 0 0 8px #ff8700;
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 530px;
  padding: 14px 0 28px;
}
@media screen and (max-width: 767px) {
  .custom-input,
.custom-textarea {
    padding: 4px 0 48px;
    width: 100%;
  }
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label,
.custom-textarea label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  max-width: 300px;
  min-height: 48px;
  margin-left: auto;
  padding: 10px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  transition: border 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
    max-width: unset;
    width: 100%;
  }
}
.custom-input label input:hover,
.custom-input label textarea:hover,
.custom-textarea label input:hover,
.custom-textarea label textarea:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-input label input:focus,
.custom-input label textarea:focus,
.custom-textarea label input:focus,
.custom-textarea label textarea:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
@media screen and (max-width: 767px) {
  .custom-input__label,
.custom-textarea__label {
    margin: 0 0 18px 0;
  }
}
.custom-input__error,
.custom-textarea__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input__error,
.custom-textarea__error {
    bottom: 20px;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea,
.custom-textarea.is-invalid input,
.custom-textarea.is-invalid textarea {
  border: 2px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover,
.custom-textarea.is-invalid textarea:focus,
.custom-textarea.is-invalid textarea:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-input.is-valid input,
.custom-textarea.is-valid input {
  border: 2px solid #22b259;
}
.custom-input.is-valid input:focus, .custom-input.is-valid input:hover,
.custom-textarea.is-valid input:focus,
.custom-textarea.is-valid input:hover {
  border: 2px solid rgba(34, 178, 89, 0.5);
}
.custom-input .input-message,
.custom-textarea .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-input .input-message.is-invalid,
.custom-textarea .input-message.is-invalid {
  color: #ff0000;
}
.custom-input .input-message.is-valid,
.custom-textarea .input-message.is-valid {
  color: #15ab07;
}

.custom-toggle {
  position: relative;
  font-size: 18px;
  line-height: 27px;
  font-weight: 300;
  display: flex;
  width: 500px;
  padding: 14px 0 10px 36px;
}
@media screen and (max-width: 767px) {
  .custom-toggle {
    padding: 14px 0 10px 0;
    width: 100%;
  }
}
.custom-toggle:last-of-type {
  padding: 14px 0 58px 36px;
}
@media screen and (max-width: 767px) {
  .custom-toggle:last-of-type {
    padding: 14px 0 58px 0;
  }
}
.custom-toggle input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-toggle label {
  position: relative;
  padding-left: 52px;
