  border-width: 0 1px 1px 0;
  transform: rotate(45deg);
}
.form__input--checkbox input:hover, .form__input--checkbox input:focus {
  outline: none;
  border-color: rgba(255, 135, 0, 0.5);
  box-shadow: 1px 1px 1px #ff8700;
}
.form__input--checkbox input:disabled {
  border: none;
  background-color: #c4c4c4;
}
@media (max-width: 1023px) {
  .form__input--checkbox input {
    width: 18px;
  }
}
@media (max-width: 767px) {
  .form__input--checkbox input {
    width: 19px;
  }
}
.form__input--checkbox span {
  max-width: 235px;
  margin: 0;
  font-size: 14px;
  line-height: 15px;
}
@media (max-width: 1023px) {
  .form__input--checkbox {
    padding-left: 0;
    padding-top: 21px;
  }
}
@media (max-width: 767px) {
  .form__input--checkbox {
    padding-top: 5px;
    padding-left: 5px;
  }
}

.form__submit-button {
  width: 100%;
  display: inline-block;
  border: none;
  cursor: pointer;
  padding: 13px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border-radius: 3px;
  color: #ffffff;
  background-color: #041427;
  user-select: none;
}
.form__submit-button:hover {
  box-shadow: 0 0 8px #ff8700;
}
.form__submit-button:focus-visible {
  outline: none;
  box-shadow: 0 0 8px #ff8700;
}
.form__submit-button:active {
  opacity: 0.7;
  background-color: #ff8700;
  box-shadow: 0 0 8px #ff8700;
}
@media (max-width: 1023px) {
  .form__submit-button {
    max-width: 265px;
    padding-top: 16px;
    padding-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .form__submit-button {
    max-width: 100%;
  }
}

